<template>
  <div>
    <van-nav-bar title="积分记录" fixed style="height: 46px">
      <van-icon name="arrow-left" size="24px" color="#666666" slot="left"  @click="$toPage(-1)" />
    </van-nav-bar>
    <div style="height: 46px;"></div>
    <div class="title-box">
      <div>日期</div>
      <div>来源</div>
      <div>积分</div>
    </div>
    <div class="placeholder-box"></div>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <div class="content-box" v-for="(item,index) in integralDataList" :key="index">
        <div>{{item.createdate}} {{item.createtime}}</div>
        <div>{{item.istatus | customRender}}</div>
        <div>{{item.integral}}</div>
      </div>
    </van-list>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        loading:false,//下拉加载
        pageIndex:1,//初始页码
        finished:false,//可以下拉加载
        integralDataList:[],//积分数据列表
      }
    },
    mounted(){
      
    },
    filters: {
      customRender: function(text){
        switch(text) {
           case 1:
          text = '签到得积分'
          break;
           case 2:
          text = '评论得积分'
          break;
           case 3:
          text = '兑换商品消耗'
          break;
           case 4:
          text = '兑换优惠券消耗'
          break;
           case 5:
          text = '下单得积分'
          break;
           case 6:
          text = '分享得积分'
          break;
           case 7:
          text = '资料认证'
          break;
           case 8:
          text = '下单消耗'
          break;
        }
        return text
      }
    },
    methods:{
      onLoad(){//下拉加载
        this.getintegralListData();
      },
      getintegralListData(){
        JNB.query({
          serverName: 'orderServer'+this.$ser_in(this.$loadUserInfo().comp.storeId),
          cls: 'IntegralModule',
          method: 'myIntegralDetail',
          params: {
            compid:this.$loadUserInfo().comp.storeId,
            pageSize:10,
            pageNo:this.pageIndex
          },
          callback :(data)=>{
            this.loading = false;//加载完成
            data = JSON.parse(data);
            if(data.code == 200){
              if(data.pageNo == 1){
                this.integralDataList = data.data;
              }else{
                this.integralDataList = this.integralDataList.concat(data.data);
              }
              if(data.total/data.pageSize<=data.pageNo){
                this.finished = true;//锁定下拉加载
              }else{
                this.pageIndex = this.pageIndex+1;
              }
            }
          },
        })
      }
    }
  }
</script>

<style scoped>
  .placeholder-box{
    height: 81px;
  }
  .title-box{
    display: flex;
    padding: 10px;
    font-size: 28px;
    color: #666666;
    line-height: 60px;
    border-bottom: 1px solid #f3ecec;
    position: fixed;
    left: 0;
    right: 0;
    background-color: #f5f5f5;
  }
  .title-box div{
    flex: 1;
    text-align: center;
  }
  .content-box{
    display: flex;
    padding:20px 10px;
    font-size: 28px;
    color: #666666;
    /* line-height: 40px; */
    border-bottom: 1px solid #f3ecec;
  }
  .content-box div{
    flex: 1;
    text-align: center;
    color: #999999;
    display: flex;
    align-items: center;
    justify-content: center;
  }
</style>
